import React from "react";

export default function ReportsPage() {
  return (
    <div style={{ padding: "32px" }}>
      <h1 style={{ fontSize: "2rem", fontWeight: 600, marginBottom: "16px" }}>Reports</h1>
      <p style={{ color: "#666", fontSize: "1rem" }}>
        This page will show enterprise reports and analytics. Please select a report from the menu or contact your administrator for access.
      </p>
    </div>
  );
}
